<template>
  <div class="project page-container overflow-hidden">
    <h1 class="project-name fs-48">{{ currentPorject.name }}</h1>
    <div class="d-flex text-wrap">
      <div class="mr-8 flex-1">
        <h3 class="mt-0 mb-text fs-24 lh-40">Company</h3>
        <p class="text-light fs-20 lh-36 mt-0 mb-48">{{ currentPorject.company }}</p>
      </div>
      <div class="flex-3">
        <h3 class="mt-0 mb-text fs-24 lh-40">My Role</h3>
        <p class="text-light fs-20 lh-36 mt-0 mb-48">{{ currentPorject.role }}</p>
      </div>
    </div>
    <div class="d-flex text-wrap mb-120">
      <div class="mr-8 flex-1">
        <h3 class="mt-0 mb-text fs-24 lh-40">Duration</h3>
        <p class="text-light fs-20 lh-36 mt-0 mb-0">{{ currentPorject.year }}</p>
      </div>
      <div class="flex-3">
        <h3 class="mt-0 mb-text fs-24 lh-40">My Deliverable</h3>
        <p class="text-light fs-20 lh-36 mt-0 mb-0">{{ currentPorject.deliverable }}</p>
      </div>
    </div>
    <template
      v-if="!!currentPorject.paragraphs && currentPorject.paragraphs.length > 0"
    >
      <div
        v-for="(v, i) in currentPorject.paragraphs"
        :key="i"
        class="info-paragraph"
      >
        <h2 class="fs-36 mb-36" v-html="v.title"></h2>
        <template v-for="(item, index) in v.content">
          <img
            :key="index + 'i'"
            v-if="item.type && item.type == 'img'"
            class="info-img"
            :class="item.class"
            :src="getImg(item.url)"
          />
          <div
            :key="index + 'r'"
            :class="item.class"
            v-else-if="item.type && item.type == 'rich'"
            v-html="item.content"
          ></div>
          <div
            :key="index + 's'"
            class="slide-box"
            :class="item.class"
            @mouseleave="resetSlide(item.name)"
            v-else-if="item.type && item.type == 'slide'"
          >
            <template v-for="(temp, idx) in item.images">
              <img
                :key="idx"
                class="slide-img"
                :class="getClass(idx, item.name)"
                :src="getImg(temp)"
                @mouseenter="onHover(idx, item.name)"
              />
            </template>
          </div>
          <div
            :key="index + 'sw'"
            class="position-relative w-100 overflow-hidden"
            :class="item.class"
            v-else-if="item.type && item.type == 'swipe'"
          >
            <img
              class="img-icon arrow-left hover-opacity-8"
              @click="onSwipe(0)"
              :src="getImg('aftersale/arrowleft.png')"
            />
            <img
              class="img-icon arrow-right hover-opacity-8"
              @click="onSwipe(1)"
              :src="getImg('aftersale/arrowright.png')"
            />
            <template v-for="(temp, idx) in item.images">
              <img
                :key="idx"
                class="w-100 swipe-img"
                :class="idx == 0 && swipeIndex ? 'swiped' : ''"
                :src="getImg(temp)"
              />
            </template>
          </div>
          <img-viewer
            :key="index + 'iv'"
            :class="item.className"
            :galleryID="item.id"
            :images="item.images"
            v-else-if="item.type && item.type == 'imgViewer'"
          />
          <p class="fs-20 fw-3 mb-36" :key="index + 'p'" v-else v-html="item"></p>
        </template>
      </div>
    </template>
    <page-footer></page-footer>
  </div>
</template>
      
      <script>
import { PROJECT_DETAILS, SLIDE_INDICES } from "@/constant";
import imgViewer from "@/components/img-viewer.vue";
import pageFooter from "@/components/page-footer.vue";
export default {
  name: "ProjectDetail",
  components: { imgViewer, pageFooter },
  data() {
    return {
      PROJECT_DETAILS,
      projectName: "",
      currentPorject: {},
      slideIndices: {},
      swipeIndex: 0,
    };
  },
  computed: {
    getImg() {
      return function (url) {
        return require(`@/assets/${url}`);
      };
    },
    getClass() {
      return function (index, name) {
        const chosen = this.slideIndices[name];
        if (index == chosen) {
          return "active";
        } else if (index == chosen + 1 || index == chosen - 1) {
          return "secondary";
        }
        return "";
      };
    },
  },
  methods: {
    onHover(index, name) {
      this.slideIndices = { ...this.slideIndices, [name]: index };
    },
    resetSlide(name) {
      this.slideIndices = { ...this.slideIndices, [name]: SLIDE_INDICES[name] };
    },
    onSwipe(val) {
      this.swipeIndex = val;
    },
  },
  mounted() {
    const {
      params: { projectName },
    } = this.$route;
    this.projectName = projectName;
    this.currentPorject = { ...this.PROJECT_DETAILS[projectName] };
    this.slideIndices = { ...SLIDE_INDICES };
  },
};
</script>
      
      <style>
@import "../style/project.scss";
</style>
      